  gap: 6px;
  margin: 10px 0;
}
.acf-quill-link-box .rel-options {
  margin-top: 5px;
}
.acf-quill-link-box footer {
  margin-top: 16px;
  text-align: right;
  display: flex;
  justify-content: space-between;
}
.acf-quill-link-box .remove-btn {
  color: #bc0b0b;
}
.acf-quill-link-box .action-btns {
  display: flex;
  gap: 8px;
}
.acf-quill-link-box .d-none {
  display: none;
}

.ql-tooltip:has(.ql-preview) {
  display: none !important;
}

/* Custom styles for Quill custom inline format dropdown */
/* Make format dropdown more user-friendly */
.ql-custom-inline {
  background-color: #f1f1f1;
  border: 1px solid #ddd;
  border-radius: 3px;
  color: #333;
  font-family: inherit;
  font-size: 14px;
  height: 24px;
  margin: 0;
  padding: 0 5px;
  min-width: 120px;
  cursor: pointer;
}

/* Improve appearance of dropdown options */
.ql-custom-inline option {
  padding: 5px;
}

/* CSS Styles for Quill Editor Info */
.quill-editor-info {
  padding: 5px 10px;
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  font-size: 12px;
  color: #666;
  display: flex;
  justify-content: space-between;
  background: #f9f9f9;
}

.quill-word-count {
  font-weight: 600;
}

.quill-path-display {
  max-width: 70%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Style for highlighting the current element in the path */
.quill-path-display .current-element {
  font-weight: bold;
  color: #0073aa;
}

/* Make the info bar responsive */
@media (max-width: 600px) {
  .quill-editor-info {
    flex-direction: column;
    gap: 5px;
  }
  .quill-path-display {
    max-width: 100%;
  }
}